import React, { useEffect, useState } from "react"
import Layout from "../component/layout"
import { css } from "@emotion/core"
import Helmet from "react-helmet"
import ISSO from "../component/comment"
import Charts from "../component/chartsCalendar.js"

const hStyle = css`
  font-size: clamp(1.3rem, 1vw + 1.3rem, 1.8rem);
  text-align: center;
  margin: 1em 0;
`
const test = css`
  width: 100vw;
  position: relative;
  left: calc(-1 * (50vw - 50%));
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
`

const chartStyle = css`
  max-width: 1450px;
  width: 95%;
  overflow: scroll;
  margin-top: 1rem;
  border-radius: 10px;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
`

const formStyle = css`
  margin: 0;
  padding: 0;
  fieldset {
    border: 1px solid grey;
  }
  fieldset legend {
    background-color: black;
    color: white;
    padding: 4px 1rem;
    font-size: 0.9rem;
    font-weight: bold;
  }
  .yearSelector {
    display: flex;
    flex-wrap: wrap;
    p {
      display: flex;
      align-items: center;
      width: 5rem;
      margin: 0.3rem 0;
    }
    p label {
      margin-left: 0.2rem;
    }
  }
`
const tipStyle = css`
  background: rgb(204, 246, 207);
  padding: 0.5rem;
  margin-top: 1rem;
  border-radius: 0.4rem;
  h3 {
    font-size: 0.9rem;
    margin: 0;
    color: rgb(44, 167, 41);
  }
  ul {
    list-style-position: outside;
    padding-left: 1rem;
    margin: 0;
  }
  ul li {
    font-size: 0.8rem;
    color: rgb(44, 167, 41);
  }
`

export default () => {
  const [history, setHistory] = useState()
  const [year, setYear] = useState(new Date().getFullYear())

  useEffect(() => {
    const url = `https://api.kejiweixun.com/api/apple_ios_history?year=${year}`
    function historyData() {
      fetch(url)
        .then(res => res.json())
        .then(res => setHistory(res))
    }
    historyData()
  }, [year])

  const years = [
    { year: 2009 },
    { year: 2010 },
    { year: 2011 },
    { year: 2012 },
    { year: 2013 },
    { year: 2014 },
    { year: 2015 },
    { year: 2016 },
    { year: 2017 },
    { year: 2018 },
    { year: 2019 },
    { year: 2020},
    { year: 2021, checked: year === 2021 },
  ]

  const inputEle = years.map(year => {
    return (
      <p key={year.year}>
        <input
          type="radio"
          name="year"
          value={year.year}
          id={year.year}
          defaultChecked={year.checked}
        />
        <label htmlFor={year.year}>{year.year}</label>
      </p>
    )
  })

  const handleChange = e => {
    setYear(e.target.value)
  }

  return (
    <Layout>
      <Helmet>
        <title>iOS 历史版本记录</title>
        <meta
          name="description"
          content="这里统计了从 2007 年至今, 每一个 iOS 版本的历史更新记录, 包括测试版和正式版. 为了方便查阅, 所有历史版本均已标注在日历视图, 今后所有新版本都会自动显示在这里."
        />
      </Helmet>
      <header>
        <h1 css={hStyle}> iOS 历史版本记录 </h1>
        <form css={formStyle} onChange={handleChange}>
          <fieldset>
            <legend>请选择年份:</legend>
            <div className="yearSelector">{inputEle}</div>
          </fieldset>
        </form>
      </header>
      <section css={tipStyle}>
        <h3>
          <span role="img" aria-label="star emoji">
            ✨
          </span>
          提示:
        </h3>
        <ul>
          <li>白色 logo 表示测试版, 彩虹 logo 表示正式版;</li>
          <li>
            iOS 13.2 之前的小版本更新没有记录在图表中, 即两个小数点的版本不记录,
            例如 13.1.1 等, 小版本从 13.2 之后开始显示;
          </li>
          <li>
            但 iOS 13.2 之后依然有些版本没有记录, 例如 iOS 13.2.1 没有记录,
            因为这个版本只面向特定用户, 大部分用户不会收到这个更新;
          </li>
          <li>
            图表中的日期是北京时间, 不保证时间 100% 准确, 但应该不会搞错吧.
          </li>
        </ul>
      </section>
      <section css={test}>
        <div css={chartStyle}>
          <Charts history={history} year={year} />
        </div>
      </section>
      <ISSO
        title="每一代 iOS 各版本历史更新记录"
        slug={`ios-version-history/`}
      />
    </Layout>
  )
}